import cookies from '@/common/cookie.js'
export default {
	data() {
		return {
			cookies: [],
			modalName: null,
			name: '',
			userhash: '',
			selectUserHash:''
		}
	},
	onLoad() {
		let _this = this
		uni.getStorage({
		    key: 'cookies',
		    success: function (res) {
		        _this.cookies = JSON.parse(res.data)
		    }
		});

	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		addCookies() {

			this.cookies.push({
				'name': this.name,
				'userhash': this.userhash
			})
			uni.setStorage({
				key: 'cookies',
				data: JSON.stringify(this.cookies),
				success() {
					uni.showToast({
						title:"添加成功！"
					})
				}
			})
			this.modalName = null
		},
		getCookies() {
			uni.getStorage({
				key: 'cookies',
				success: function(res) {
					
					this.cookies = JSON.parse(res.data)
					
				}
			});
		},
		inputCookiesName(e) {
			this.name = e.detail.value
		},
		inputUserHash(e) {
			this.userhash = e.detail.value
		},
		RadioChange(evt){
			this.selectUserHash = evt.target.value
		},
		addUserHash(){
			uni.setStorage({
				key: 'cookie',
				data: this.selectUserHash,
				success() {
					uni.showToast({
						title:"添加成功！"
					})
				}
			})
		}
	}
}
